import React from 'react';

const HeroSection = () => {
  return (
    <div style={{display:"flex"}}>
    <section style={styles.hero}>
      <div className="container">
        <div style={styles.heroContent}>
          <h1 style={styles.title}>   Drupal</h1>
          <p style={styles.subtitle}>     CMS Drupal    </p>
          <a style={styles.ctaButton} href="#tariffs"> </a>
        </div>
      </div>
    </section>
    <section style={styles.hero} >
      <ul style={{display:"flex",flexflow:"column warp",maxWidth:"350px",width:"100%",gap:"80px",marginTop:"50px"}}>
        <li style={{width:"200px",listStyle:"none"}}>Drupal-      </li>
        <li  style={{width:"200px",listStyle:"none"}}>    3 </li>
      </ul>
 <ul style={{display:"flex",flexflow:"column warp",maxWidth:"350px",width:"100%",gap:"80px", marginTop:"20px"}}>
      <li  style={{width:"200px",listStyle:"none"}}>200+      DrupalGive</li>
        <li  style={{width:"200px",listStyle:"none"}}>35000     Drupal</li>
        </ul>
    </section>
    </div>
  );
};

const styles = {
  hero: {
    color: '#fff',
    padding: '200px 0',
  },
  heroContent: {
    maxWidth: '800px',
    margin: '0 auto',
  },
  title: {
    fontSize: '66px',
    marginBottom: '20px',
    lineHeight: '1.3',
  },
  subtitle: {
    fontSize: '18px',
    marginBottom: '30px',
    opacity: '0.9',
  },
  ctaButton: {
    backgroundColor: '#ff6b00',
    color: '#fff',
    border: 'none',
    padding: '15px 40px',
    fontSize: '18px',
    borderRadius: '5px',
    cursor: 'pointer',
    fontWeight: 'bold',
  }
};

export default HeroSection;