import React from 'react';
import './MobileMenu.css';

const MobileMenu = ({ isOpen, onClose }) => {
  return (
    <div className={`mobile-menu-overlay ${isOpen ? 'open' : ''}`}>
      <div className="mobile-menu">
        <button className="close-menu" onClick={onClose}></button>
        <nav>
          <ul className="mobile-menu-list">
            <li><a href="#services" onClick={onClose}></a></li>
            <li><a href="#tariffs" onClick={onClose}></a></li>
            <li><a href="#tariffs" onClick={onClose}></a></li>
            <li><a href="#services" onClick={onClose}></a></li>
            <li><a href="#faq" onClick={onClose}>FAQ</a></li>
            <li><a href="#contact" onClick={onClose}></a></li>
          </ul>
        </nav>
      </div>
    </div>
  );
};

export default MobileMenu;